#frame{
    display: flex;
    align-items: center;
    align-items: center;
    flex-direction: column;
    background-color: rgb(246, 242, 241);
    width: 250px;
    height: 350px;
    box-shadow: 2px 2px hsl(16, 30%, 39%, 0.3);
    border-radius: 10px;
    text-align: center;
}
#product_name{
    margin-top: 20px;
    font-size: 1em;
}

#product_img{
    margin-top: 10px;
    height:250px;
}

#product_link{
    margin-top: 10px;
    font-size: 20px;
    text-decoration: none;
    width: 200px;
    background-color: #FFD814;
    border-radius: 50px;
    padding: 3px;
}
#product_link:hover{
    background-color: #F7CA00;
    color: hsl(16, 29%, 10%);
}

#product_list{
    justify-content: center;
    margin: 15px;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

#loading_animation{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#OutfitTitle{
    margin-left: 10px;
    text-align: center;
}