#layout{
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
}
#navbar{
    text-align: center;
    margin-left: auto;
    background-color: rgb(246, 242, 241);
    width: 100%;
    height: min-content;
    position: sticky;
    top: 0;
}
#menus{
    display: flex;
    background-color: rgb(246, 242, 241);
    width: 100%;
    height:2em;
    box-shadow: 0px 5px hsl(16, 30%, 39%, 0.3);
    align-items: inline;
    z-index: 5;
}
#content{
    /*background-color: rgb(246, 233, 233);*/
    width: 100%;
}
h1{
    margin: 0;
    color: hsl(16, 29%, 10%);
}

a{
    text-decoration: underline;
    color: hsl(16, 29%, 10%);
}

a:hover{
    color: hsl(36, 75%, 56%);
}

#section{
    width: 50%;
    text-align: center;
    font-size: 1em;
}

#footer{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #211612;
    width: 100%;
    min-height: 20%;
    color: rgb(246, 242, 241);
}

#icons{
    color: rgb(246, 242, 241);
    width: 50px;
    font-size: 50px;
}

#container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

#home_btn{
    text-decoration: none;
}
#home_btn:hover{
    color: hsl(16, 29%, 10%);
}

#menu_wrapper{
    margin: 10px;
    display: flex;
    flex-direction: row;
    width: 100%;
    gap: 20px;
    align-items: center;
    justify-content: center;
    padding: 5px;
}

#menu_choose{
    background-color: rgb(246, 242, 241);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: right;
    padding: 5px;
    width: 400px;
    height: 400px;
    box-shadow: 2px 2px hsl(16, 30%, 39%, 0.3);
}